import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

const PracticalCodeImage = () => {
    const data = useStaticQuery(graphql`
    query {
      practicalCodeImage: file(relativePath: { eq: "practical-code.png" }) {
        childImageSharp {
          fluid(maxWidth: 880) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

    return <Img fluid={data.practicalCodeImage.childImageSharp.fluid} />
}

export default PracticalCodeImage
